<template>
  <div class="table-content">
    <div slot="header" class="title">基本信息</div>
    <table class="table" cellspacing="0">
      <tr>
        <td
          rowspan="4"
          class="portrait"
          :style="{ backgroundImage: `url(${info.avatar})` }"
        />
        <td>钉钉ID</td>
        <td>{{ info.userid }}</td>
        <td>学员姓名</td>
        <td>{{ info.name }}</td>
        <td>首次登陆时间</td>
        <td>{{ getTime(info.insertTime) }}</td>
        <td>使用计划</td>
        <td>{{ info.plan_name || '无' }}</td>
      </tr>
      <tr>
        <td>当前阶段</td>
        <td>{{ getStage(info.stage, info.job_rotation_position) }}</td>
        <td>阶段状态</td>
        <td>{{ getStageStatus(info.stage_status) }}</td>
        <td>绑定医生</td>
        <td>{{ info.doctor || '/' }}</td>
        <td>第一阶段</td>
        <td>
          {{
            info.plans &&
            info.plans[0] &&
            (info.plans[0].score || info.plans[0].score === 0)
              ? info.plans[0].score
              : '/'
          }}
        </td>
      </tr>
      <tr>
        <td>第二阶段</td>
        <td>
          {{
            info.plans &&
            info.plans[1] &&
            (info.plans[1].score || info.plans[1].score === 0)
              ? info.plans[1].score
              : '/'
          }}
        </td>
        <td>第三阶段</td>
        <td>
          {{
            info.plans &&
            info.plans[2] &&
            (info.plans[2].score || info.plans[2].score === 0)
              ? info.plans[2].score
              : '/'
          }}
        </td>
        <td>第四阶段</td>
        <td>
          {{
            info.plans &&
            info.plans[3] &&
            (info.plans[3].score || info.plans[3].score === 0)
              ? info.plans[3].score
              : '/'
          }}
        </td>
        <td>第五阶段</td>
        <td>
          {{
            info.plans &&
            info.plans[4] &&
            (info.plans[4].score || info.plans[4].score === 0)
              ? info.plans[4].score
              : '/'
          }}
        </td>
      </tr>
      <tr>
        <td>第六阶段</td>
        <td>
          {{
            info.plans &&
            info.plans[5] &&
            (info.plans[5].score || info.plans[5].score === 0)
              ? info.plans[5].score
              : '/'
          }}
        </td>
        <td>试岗用时</td>
        <td>{{ trialTime(info.test_job_at) }}</td>
      </tr>
    </table>
  </div>
</template>

<script>
export default {
  props: {
    info: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  computed: {
    trialTime() {
      return function(usedTime) {
        const days = Math.floor(usedTime / (24 * 3600 * 1000)) // 计算出天数
        const leavel = usedTime % (24 * 3600 * 1000) // 计算天数后剩余的时间
        const hours = Math.floor(leavel / (3600 * 1000)) // 计算剩余的小时数
        const leavel2 = leavel % (3600 * 1000) // 计算剩余小时后剩余的毫秒数
        const minutes = Math.ceil(leavel2 / (60 * 1000)) // 计算剩余的分钟数
        return days + '天' + hours + '时' + minutes + '分'
      }
    },
    getTime() {
      return function(time) {
        if (!time) return ''
        const str = time.split('T')
        return str[0] + ' ' + str[1].split('.')[0]
      }
    },
    getStageStatus() {
      return function(stage_status) {
        if (stage_status === 'doing') return '正在进行'
        if (stage_status === 'exam') return '待考试'
        if (stage_status === 'eliminate') return '淘汰'
        if (stage_status === 'delay') return '延期'
        if (stage_status === 'pass') return '通过'
        else return ''
      }
    },
    getStage() {
      return function(stage, job_rotation_position) {
        if (stage === job_rotation_position) return '轮岗阶段'
        else {
          if (stage === -1) return '试岗阶段'
          if (stage < job_rotation_position) {
            if (stage === 0) return '第一阶段'
            if (stage === 1) return '第二阶段'
            if (stage === 2) return '第三阶段'
            if (stage === 3) return '第四阶段'
            if (stage === 4) return '第五阶段'
            if (stage === 5) return '第六阶段'
          }
          if (stage > job_rotation_position) {
            stage = stage - job_rotation_position
            if (stage === 0) return '第二阶段'
            if (stage === 1) return '第三阶段'
            if (stage === 2) return '第四阶段'
            if (stage === 3) return '第五阶段'
            if (stage === 4) return '第六阶段'
            if (stage === 5) return '第七阶段'
          }
        }
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.table-content {
  padding: 20px;
  font-size: 14px;
  border: 1px solid #f5f7fa;
  .table {
    width: 100%;
    height: 190px;
    text-align: center;
    border: 1px solid #ebeef5;
    border-radius: 4px;
    tr {
      height: 46px;
      td {
        min-width: 100px;
        border-bottom: 1px solid #ebeef5;
      }
      td:nth-child(2n + 1) {
        color: #17233d;
        background: rgba(246, 247, 251, 0.64);
      }
      td:nth-child(2n) {
        color: #576277;
      }
      .portrait {
        width: 160px;
        background-position: center center !important;
        background-size: 160px 190px !important;
        border: none;
      }
      &:first-child {
        td:nth-child(2n) {
          color: #17233d;
          background: rgba(246, 247, 251, 0.64);
        }
        td:nth-child(2n + 1) {
          color: #576277;
          background-color: #fff;
        }
      }
      &:last-child td {
        border: none;
      }
    }
  }
  .student-info {
    display: flex;
    width: 100%;
    height: 190px;
    border: 1px solid #ebeef5;
    flex-wrap: nowrap;
    .img {
      width: 160px;
    }
    .info {
      display: flex;
      overflow-x: auto;
      width: 100%;
      height: 190px;
    }
    .fixed-right {
      width: 160px;
    }
    dl {
      height: 100%;
      &:nth-child(2n + 1) {
        background: rgba(246, 247, 251, 0.64);
      }
      dd {
        width: 175px;
        height: 46px;
        line-height: 46px;
        text-align: center;
        border-bottom: #ebeef5;
      }
    }
  }
}
</style>
