<template>
  <!-- 阶段考试结果 -->
  <div class="ph-ex-result">
    <div class="title">
      <div>{{ info.stage === -1 ? '试岗期考核结果' : '各阶段考试结果' }}</div>
      <el-button v-if="info.stage !== -2" type="primary" @click="seeHistory"
        >查看历史记录</el-button
      >
    </div>
    <el-form
      ref="ruleForm"
      :model="ruleForm"
      :rules="rules"
      label-width="120px"
      class="demo-ruleForm"
    >
      <el-form-item v-if="info.stage !== -1" label="考核阶段：" prop="stage">
        <el-input
          v-model.number="ruleForm.stage"
          type="stage"
          autocomplete="off"
          width="220"
          placeholder="自动获取"
          disabled
          class="input-stage"
        />
      </el-form-item>
      <el-form-item
        v-if="info.stage === -1"
        label="培训师评价："
        prop="comment"
      >
        <el-input
          v-model.trim="ruleForm.comment"
          type="textarea"
          placeholder="请输入评价内容  如：性格、学习能力、业务能力等方面给出客观评价"
          maxlength="500"
          show-word-limit
          rows="4"
          resize="none"
          :disabled="commentDisabled"
        />
      </el-form-item>
      <el-form-item
        v-if="info.job_rotation_position !== info.stage"
        label="考核结果："
        prop="type"
      >
        <el-radio-group v-model="ruleForm.type">
          <el-radio
            :label="info.stage === -1 ? '不通过' : '淘汰'"
            @click.native="isEliminate = true"
          />
          <el-radio
            v-if="info.stage !== -1"
            label="延期再学习"
            @click.native="isEliminate = true"
          />
          <el-radio label="通过" @click.native="isEliminate = false" />
        </el-radio-group>
      </el-form-item>
      <el-form-item
        v-if="info.stage !== -1 && info.job_rotation_position !== info.stage"
        label="分数："
        prop="score"
      >
        <el-input
          v-model.number="ruleForm.score"
          type="point"
          autocomplete="off"
          width="220"
          placeholder="请输入分数"
          class="input-stage"
        />
      </el-form-item>
      <el-form-item
        v-if="info.stage === info.job_rotation_position || isEliminate"
        :label="
          info.stage === info.job_rotation_position
            ? '医生评价：'
            : info.stage === -1
            ? '不通过原因：'
            : ruleForm.type === '延期再学习'
            ? '延期原因：'
            : '淘汰原因：'
        "
        prop="reason"
      >
        <el-input
          v-model.trim="ruleForm.reason"
          type="textarea"
          :placeholder="
            info.stage === info.job_rotation_position
              ? '请对该健康管理师在轮岗期的表现进行评价'
              : info.stage === -1
              ? '请填写不通过的其他原因'
              : ruleForm.type === '延期再学习'
              ? '请填写延期原因'
              : '请填写淘汰该健康管理师的其他原因'
          "
          maxlength="200"
          show-word-limit
          rows="4"
          resize="none"
          :disabled="
            info.stage === info.job_rotation_position &&
              $store.state.user.mts_user.type !== 'doctor'
          "
        />
      </el-form-item>
      <el-form-item>
        <el-button
          type="primary"
          :disabled="noSubmit"
          @click="submitForm('ruleForm')"
          >提交</el-button
        >
      </el-form-item>
    </el-form>
    <el-divider v-if="isShowComment" />
    <!-- 评论 -->
    <!--   v-if="info.job_rotation_position === info.stage || info.stage === -1"  -->
    <div v-if="isShowComment" v-loading="loading" class="tip">
      <template v-if="!loadingComment">
        <p class="tip-info center">
          目前已有{{ commentNumber.commented.length }}人提交评价
          <span v-if="commentNumber.un_comment.length">
            ，未进行评价的人是：
            <span v-for="(item, index) of commentNumber.un_comment" :key="index"
              >{{ item.name }} ，</span
            >
            请进行联系处理
          </span>
        </p>
        <p class="center">
          <el-button type="primary" @click="getComment">
            {{ info.stage === -1 ? '获取最终结果' : '获取全部评论' }}
          </el-button>
        </p>
      </template>
      <template
        v-if="commentList.length && loadingComment && info.stage !== -1"
      >
        <h4 class="comment-title">评价详情</h4>
        <p
          v-for="(item, index) of commentList"
          :key="index"
          class="comment-info"
        >
          {{ item.admin.name }}评价：
          <span class="text">{{ item.comment }}</span>
        </p>
      </template>
      <template v-if="trailInfo && loadingComment && info.stage === -1">
        <h4 class="final-result">
          最终结果：{{ isPass(trailInfo.stage_status) }}
        </h4>
        <p class="result-tip">
          本次考核有效评论数为{{
            trailInfo.commented + trailInfo.un_comment.length
          }}条， 通过个数为{{ trailInfo.commented }}，不通过个数为{{
            trailInfo.un_comment.length
          }}
        </p>
        <div v-if="trailInfo.un_comment.length" class="result-info">
          <div class="reason-title">不通过原因为：</div>
          <div class="reason-info">
            <p v-for="(item, index) of trailInfo.un_comment" :key="index">
              原因{{ index + 1 }}：{{ item.reason }}
            </p>
          </div>
        </div>
      </template>
    </div>
    <!-- 提交确认 -->
    <el-dialog
      title="提示"
      :visible.sync="isSubmit"
      width="30%"
      center
      top="30vh"
    >
      <p class="center">提交后，将不能再更改，请确认是否提交？</p>
      <span slot="footer" class="dialog-footer">
        <el-button @click="isSubmit = false">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 历史记录 -->
    <el-dialog
      title="各阶段考试结果"
      :visible.sync="historyShow"
      center
      class="history-info"
      top="30vh"
    >
      <el-table :data="historyList">
        <el-table-column
          property="stage"
          label="阶段"
          width="150"
          align="center"
        />
        <el-table-column
          property="name"
          label="处理人"
          width="150"
          align="center"
        />
        <el-table-column
          property="score"
          label="分数"
          width="150"
          align="center"
        />
        <el-table-column
          property="stage_status"
          label="考核结果"
          width="150"
          align="center"
        />
        <el-table-column property="content" label="备注" align="center" />
      </el-table>
    </el-dialog>
  </div>
</template>

<script>
import {
  trailResult,
  getCommentResult,
  stageComment,
  addComment,
  roundComment,
  userIsComment
} from '@/api/learningAdmin'

export default {
  props: {
    stage: {
      type: String,
      default: ''
    },
    info: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data() {
    return {
      ruleForm: {
        stage: '',
        comment: '',
        type: [],
        reason: '',
        score: ''
      },
      rules: {
        reason: [
          {
            required: true,
            message: '您输入内容有误，请重新输入',
            trigger: 'blur'
          }
        ],
        stage: [{ required: true, message: '阶段不能为空' }],
        type: [
          { required: true, message: '请选择一个考核结果', trigger: 'change' }
        ],
        comment: [
          {
            required: true,
            message: '您输入内容有误，请重新输入',
            trigger: 'blur'
          }
        ],
        score: [
          { required: true, message: '分数不能为空' },
          { type: 'number', message: '分数必须为数字值' }
        ]
      },
      historyList: [],
      commentList: [],
      isSubmit: false,
      historyShow: false,
      loadingComment: false,
      loading: false,
      noSubmit: false, // 是否可以提交(按钮状态)
      commentDisabled: false, // 评论框禁止
      user: this.$store.state.user.mts_user,
      isEliminate: false,
      trailInfo: '', // 试岗最终结果
      commentNumber: {
        // 初始化评论数量数据
        commented: [],
        un_comment: []
      }
    }
  },
  computed: {
    getStageStatus() {
      return function(stage_status) {
        if (stage_status === 'doing') return '正在进行'
        if (stage_status === 'exam') return '待考试'
        if (stage_status === 'eliminate') return '淘汰'
        if (stage_status === 'delay') return '延期'
        if (stage_status === 'pass') return '通过'
        return '/'
      }
    },
    getStage() {
      return function(stage, job_rotation_position) {
        if (stage === job_rotation_position) return '轮岗阶段'
        else {
          if (stage === -1) return '试岗阶段'
          if (stage < job_rotation_position) {
            if (stage === 0) return '第一阶段'
            if (stage === 1) return '第二阶段'
            if (stage === 2) return '第三阶段'
            if (stage === 3) return '第四阶段'
            if (stage === 4) return '第五阶段'
            if (stage === 5) return '第六阶段'
          }
          if (stage > job_rotation_position) {
            stage = stage - job_rotation_position
            if (stage === 0) return '第二阶段'
            if (stage === 1) return '第三阶段'
            if (stage === 2) return '第四阶段'
            if (stage === 3) return '第五阶段'
            if (stage === 4) return '第六阶段'
            if (stage === 5) return '第七阶段'
          }
        }
      }
    },
    isPass() {
      return function(str) {
        if (str === 'pass') return '通过'
        if (str === 'eliminate') return '不通过'
      }
    },
    getStatus() {
      return function(str) {
        if (str === '通过') return 'pass'
        if (str === '不通过') return 'eliminate'
        if (str === '淘汰') return 'eliminate'
        if (str === '延期再学习') return 'delay'
      }
    },
    isShowComment() {
      if (this.info.stage === -1 && this.user.type === 'human_resource') {
        return true
      } else if (
        this.info.stage === this.info.job_rotation_position &&
        this.user.type === 'teacher'
      ) {
        return true
      } else return false
    }
  },
  watch: {
    stage() {
      this.ruleForm.stage = this.stage
    },
    async info() {
      this.ruleForm.stage = this.getStage(
        this.info.stage,
        this.info.job_rotation_position
      )
      this.getStageCommentResult()
      if (this.info.stage === -1) this.getTeacherComment()
      this.userIsComment(this.info.stage)
    }
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.isSubmit = true
        } else {
          return false
        }
      })
    },
    async submit() {
      if (this.info.stage === -1 && this.user.type === 'teacher') {
        const { data: data1 } = await addComment({
          comment: this.ruleForm.comment,
          stage_status: this.getStatus(this.ruleForm.type),
          user: this.info._id,
          reason: this.ruleForm.reason
        })
        if (data1) {
          if (data1.result) {
            this.$message({ message: '评论成功', type: 'success' })
          } else this.$message({ message: data1.message, type: 'error' })
          this.noSubmit = true
        }
      }
      if (this.info.stage === -1 && this.user.type !== 'teacher') {
        const { data: data2 } = await addComment({
          stage_status: this.getStatus(this.ruleForm.type),
          user: this.info._id,
          reason: this.ruleForm.reason
        })
        if (data2) {
          if (data2.result) {
            this.$message({ message: '评论成功', type: 'success' })
          } else this.$message({ message: data2.message, type: 'error' })
          this.noSubmit = true
        }
      }
      if (this.info.stage !== -1) {
        if (this.info.stage === this.info.job_rotation_position) {
          const { data: data3 } = await addComment({
            comment: this.ruleForm.reason,
            user: this.info._id
          })
          if (data3) {
            if (data3.result) {
              this.$message({ message: '评论成功', type: 'success' })
            } else this.$message({ message: data3.message, type: 'error' })
          }
          this.noSubmit = true
        } else {
          const { data: data4 } = await addComment({
            comment: this.ruleForm.comment,
            stage_status: this.getStatus(this.ruleForm.type),
            user: this.info._id,
            reason: this.ruleForm.reason,
            score: this.ruleForm.score
          })
          if (data4) {
            if (data4.result) {
              this.$message({ message: '评论成功', type: 'success' })
            } else {
              this.$message({ message: data4.message, type: 'error' })
            }
          }
          this.noSubmit = true
        }
      }
      this.isSubmit = false
    },
    async getResult() {
      // 获取试岗结果
      const { data } = await trailResult({
        user: this.info._id
      })
      if (data) {
        if (data.result) {
          this.trailInfo = data
          this.loadingComment = true
        } else this.$message({ message: data.message, type: 'error' })
      }
    },
    async seeHistory() {
      // 查看历史记录
      const { data } = await stageComment({
        user: this.info._id
      })
      if (data && data.result && data.list.length > 0) {
        this.historyList = data.list.map(item => {
          return {
            stage: this.getStage(item.stage, this.info.job_rotation_position),
            name: item.admin.name,
            score: item.score || '/',
            stage_status: this.getStageStatus(item.stage_status),
            content: item.comment || '/'
          }
        })
      }
      this.historyShow = true
    },
    async getComment() {
      // 获取最终结果或获取全部评论
      this.loading = true
      if (this.info.stage === -1) {
        await this.getResult()
      }
      if (this.info.stage !== -1 && this.info.stage !== this.info.status) {
        await this.getStageCommentList()
      }
      if (
        this.info.stage !== -1 &&
        this.info.stage === this.info.job_rotation_position
      ) {
        const { data } = await roundComment({
          user: this.info._id
        })
        if (!data.result) {
          this.$message({ message: data.message, type: 'error' })
        }
      }
      this.loading = false
    },
    async getStageCommentResult() {
      // 获取阶段评论结果
      const { data } = await getCommentResult({
        user: this.info._id,
        stage: this.info.stage
      })
      if (data && data.result) this.commentNumber = data
    },
    async getStageCommentList() {
      // 获取阶段评价列表
      const { data } = await stageComment({
        user: this.info._id,
        stage: this.info.stage
      })
      if (data && data.result) {
        this.commentList = data.list
        if (data.result && data.list.length === 0) {
          return this.$message({ message: '暂无评论', type: 'error' })
        } else this.loadingComment = true
      }
    },
    async getTeacherComment() {
      // 获取培训师的评论 来改变状态
      const { data } = await stageComment({
        user: this.info._id,
        stage: this.info.stage
      })
      if (data && data.result) {
        const teacherComment = data.list.filter(item => {
          return item.stage === -1 && item.type === 'teacher'
        })
        if (teacherComment.length > 0) {
          this.ruleForm.comment = teacherComment[0].comment
          this.commentDisabled = true
          if (this.user.type === 'teacher') this.noSubmit = true
        }
        if (teacherComment.length === 0 && this.user.type !== 'teacher') {
          this.noSubmit = true
        }
      }
    },
    async userIsComment(stage) {
      const { code, data } = await userIsComment({
        user: this.$route.params.id,
        stage
      })
      if (code === 20000 && data.result) this.noSubmit = data.is_review
    }
  }
}
</script>

<style lang="scss" scoped>
.ph-ex-result {
  margin: 20px 0;
  padding: 20px;
  border: 1px solid #ebeef5;
  .title {
    display: flex;
    justify-content: space-between;
    line-height: 32px;
  }
  /deep/button {
    padding: 6px 20px;
    height: 32px;
  }
  .input-stage {
    width: 220px;
    height: 32px;
    /deep/input {
      height: 32px;
    }
  }
  .history-info {
    /deep/.el-dialog__headerbtn {
      top: 10px;
      right: 5px;
    }
  }
  .comment-title {
    font-size: 16px;
    color: #17233d;
    margin-bottom: 12px;
  }
  .comment-info {
    font-size: 12px;
    color: #17233d;
    margin-bottom: 8px;
    .text {
      color: #576277;
    }
  }
  .tip {
    overflow-y: auto;
    margin-left: 112px;
    padding: 12px 14px;
    height: 100px;
    border: 1px solid #ebeef5;
    border-radius: 5px;
    .tip-info {
      margin: 10px 0;
      font-size: 14px;
      color: #f98790;
    }
    .final-result {
      font-size: 16px;
      color: #0085ff;
      margin-bottom: 8px;
    }
    .result-tip {
      font-size: 12px;
      color: #17233d;
      margin-bottom: 8px;
    }
    .result-info {
      display: flex;
      font-size: 12px;
      color: #576277;
      p {
        margin-bottom: 8px;
      }
    }
  }
}
</style>
