<template>
  <div class="study-info">
    <div class="title">学习信息</div>
    <el-table :data="records" empty-text="暂无数据" style="width: 100%">
      <el-table-column
        fixed
        prop="start_time"
        label="开始时间"
        min-width="120"
        align="center"
      />
      <el-table-column
        prop="pass_time"
        label="通过时间"
        min-width="120"
        align="center"
      />
      <el-table-column
        prop="study_content"
        label="学习内容"
        min-width="120"
        align="center"
      />
      <el-table-column
        prop="stage"
        label="所属阶段"
        min-width="100"
        align="center"
      />
      <el-table-column
        prop="score"
        label="考试成绩"
        min-width="80"
        align="center"
      />
      <el-table-column
        prop="num"
        label="当前次数"
        min-width="80"
        align="center"
      />
      <el-table-column
        fixed="right"
        prop="study_time"
        label="学习用时"
        min-width="100"
        align="center"
      />
    </el-table>
    <div class="pagination">
      <el-pagination
        :current-page="currentPage"
        :page-sizes="[12, 18, 24]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="count"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      />
    </div>
  </div>
</template>

<script>
import { getUerDetail } from '@/api/learningAdmin'
import { getNowFormatDate } from '@/utils/learningMethod'
export default {
  props: {
    tableData: {
      type: Array,
      default: () => {
        return []
      }
    },
    workShift: {
      type: Number,
      default: -2
    },
    count: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      currentPage: 1,
      records: [],
      pageSize: 12
    }
  },
  computed: {
    getTime() {
      return function(start, end) {
        const startTime = new Date(start).getTime() // 开始时间
        const endTime = new Date(end).getTime() // 结束时间
        const usedTime = endTime - startTime // 相差的毫秒数
        if (usedTime < 0) return '0天0时0分'
        const days = Math.floor(usedTime / (24 * 3600 * 1000)) // 计算出天数
        const leavel = usedTime % (24 * 3600 * 1000) // 计算天数后剩余的时间
        const hours = Math.floor(leavel / (3600 * 1000)) // 计算剩余的小时数
        const leavel2 = leavel % (3600 * 1000) // 计算剩余小时后剩余的毫秒数
        const minutes = Math.ceil(leavel2 / (60 * 1000)) // 计算剩余的分钟数
        const timeStr = days + '天' + hours + '时' + minutes + '分'
        if (timeStr.indexOf('NaN') !== -1) return '/'
        return timeStr
      }
    },
    getStage() {
      return function(stage, job_rotation_position) {
        if (stage === job_rotation_position) return '轮岗阶段'
        else {
          if (stage === -1) return '试岗阶段'
          if (stage < job_rotation_position) {
            if (stage === 0) return '第一阶段'
            if (stage === 1) return '第二阶段'
            if (stage === 2) return '第三阶段'
            if (stage === 3) return '第四阶段'
            if (stage === 4) return '第五阶段'
            if (stage === 5) return '第六阶段'
          }
          if (stage > job_rotation_position) {
            stage = stage - job_rotation_position
            if (stage === 0) return '第二阶段'
            if (stage === 1) return '第三阶段'
            if (stage === 2) return '第四阶段'
            if (stage === 3) return '第五阶段'
            if (stage === 4) return '第六阶段'
            if (stage === 5) return '第七阶段'
          }
        }
      }
    },
    getType() {
      return function(str) {
        if (str === 'professional_ability') return '专业能力'
        if (str === 'typing_speed') return '打字速度'
        if (str === 'problem_diagnosis_ability') return '用户问题确诊能力'
        if (str === 'summarizing_ability') return '学习总结能力'
      }
    }
  },
  watch: {
    tableData() {
      this.records = this.tableData.map(item => {
        return {
          start_time: getNowFormatDate(item.exam_start_at || item.started_at),
          pass_time: getNowFormatDate(item.exam_end_at || item.ended_at),
          study_content: item.catalog
            ? item.catalog.title
            : item.lesson_column
            ? this.getType(item.lesson_column)
            : '/',
          stage: this.getStage(item.stage, this.workShift) || '/',
          score: item.score || item.word_count || '0',
          num: item.exam_count || 1,
          study_time: this.getTime(
            item.started_at,
            item.study_at || item.ended_at
          )
        }
      })
    }
  },
  methods: {
    handleSizeChange(val) {
      this.pageSize = val
      this.getDetail()
    },
    handleCurrentChange(val) {
      this.currentPage = val
      this.getDetail()
    },
    async getDetail() {
      const { data, code } = await getUerDetail({
        user: this.$route.params.id,
        pageSize: this.pageSize,
        page: this.currentPage
      })
      if (data && code === 20000) {
        this.records = data.records.map(item => {
          return {
            start_time: getNowFormatDate(item.exam_start_at || item.started_at),
            pass_time: getNowFormatDate(item.exam_end_at || item.ended_at),
            study_content: item.catalog
              ? item.catalog.title
              : item.lesson_column
              ? this.getType(item.lesson_column)
              : '/',
            stage: this.getStage(item.stage, this.workShift) || '/',
            score: item.score || item.word_count || '0',
            num: item.exam_count || 1,
            study_time: this.getTime(
              item.started_at,
              item.study_at || item.ended_at
            )
          }
        })
        this.count = data.count
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.study-info {
  margin: 20px 0;
  padding: 20px;
  border: 1px solid #ebeef5;
  .pagination {
    margin-top: 50px;
    text-align: center;
  }
  /deep/.cell {
    overflow: hidden;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}
</style>
