<template>
  <div class="students">
    <div class="close clearfix">
      <i
        class="el-icon-circle-close"
        @click="$router.push('/learning-admin/studentsList')"
      />
    </div>
    <student-info :info="info" />
    <ph-ex-result v-if="$route.name === 'phExResult'" :info="info" />
    <study-info
      :table-data="records"
      :work-shift="info.job_rotation_position"
      :count="info.count"
    />
  </div>
</template>

<script>
import StudentInfo from './components/StudentInfo'
import PhExResult from './components/PhExResult'
import StudyInfo from './components/StudyInfo'
import { getUerDetail } from '@/api/learningAdmin'
export default {
  components: {
    StudentInfo,
    StudyInfo,
    PhExResult
  },
  data() {
    return {
      info: {},
      type: this.$store.state.user.mts_user.type,
      records: []
    }
  },
  created() {
    this.getDetail()
  },
  methods: {
    async getDetail() {
      const { data } = await getUerDetail({
        user: this.$route.params.id,
        page: 1,
        pageSize: 12
      })
      if (data) {
        this.info = data
        this.records = data.records
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.students {
  .close {
    i {
      float: right;
      font-size: 24px;
      color: #409eff;
      margin-bottom: 20px;
      cursor: pointer;
    }
  }
}
</style>
